.messages {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: none;
}

/* Navbar 
 -------------------------------------------------------- 
Add a black background color to the top navigation / */
.topnav {
  background-color: #33383b;
  overflow: hidden;
  padding: 10px;
}

/* Style the links inside the navigation bar / */
.topnav a {
  background-color: white;
  color: #B59410;
  float: right;
  text-align: center;
  padding: 10px 15px;
  margin-right: 15px;
  text-decoration: none;
  font-size: 17px;
  border-radius: 14px;
}
/* Change the color of links on hover */
.topnav a:hover {
    background-color: #d1c8c3;
    color: #6b9e7c;
  }


/* Footer Container  */
.footer-distributed {
  background: rgb(107, 158, 124);
  box-shadow: 0 1px 1px 0 rgba(93, 54, 54, 0.12);
  box-sizing: border-box;
  width: 100%;
  font: bold 16px sans-serif;
  text-align: left;
  padding: 30px 50px 50px 50px;
  margin-top: 60px;
  overflow: hidden;
}


