/* The Footer Name (Mylo)  */
.footer-distributed h3 {
  color: #ffffff;
  font: normal 36px "Roboto", cursive;
  margin: 0 0 30px;
}
/* The Footer Name (Chat) */
.footer-distributed h3 span {
  color: #163771;
}

/* Footer Links (Home/About/Contact) */
.footer-distributed .footer-links {
  color: #ffffff;
  margin: 0 0 10px;
  padding: 0;
}
.footer-distributed .footer-links a {
  display: inline-block;
  line-height: 1.8;
  text-decoration: none;
  color: inherit;
}

/* Footer (MyloChat Inc.) */
.footer-distributed .footer-company-name {
  color: #ffffff;
  font-size: 20px;
  font-weight: bolder;
  margin: 0;
}

/* Footer social icons (GitGub) */
.footer-distributed .footer-icons a {
  display: inline-block;
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: #ffffff;
  border-radius: 20px;
  font-size: 10px;
  text-align: center;
  line-height: 40px;
} 

/* About Us Section (About Us)*/
.aboutbody {
    width: 200vh;
    margin: 0;
    padding: 0;
}

.about-section {
    padding: 10px;
    margin-bottom: 30px;
